.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
}

.card__image {
  background-image: url("imac.202c52ff.jpeg");
  background-size: 160px 134px;
  width: 160px;
  height: 134px;
  margin: 32px 19px 40px;
}

.card__title {
  color: #060b35;
  margin: 0 16px 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  font-weight: 400px;
  color: #616070;
  margin: 0 16px 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__stars {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-bottom: 24px;
  margin-left: 16px;
  margin-right: 16px;
  display: flex;
}

.card__stars-review {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin: 0 16px 16px;
  display: flex;
}

.card__price-name {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-price {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy {
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  margin: 0 16px 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-left: 4px;
}

.stars__star:first-child {
  margin-left: 0;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  display: flex;
}
